//  Creacin de materiales
const vidrioMaterial = new THREE.MeshStandardMaterial({
    transparent: true,
    roughness: .8,
    metalness: 0.1,
    opacity: 0.8,
    color: "#d6d6d6",
    side:THREE.DoubleSide
});

//  Agua
const aguaTexture = new THREE.TextureLoader().load(require(
    '../../../assets/models/mall/Textures/Agua/CC_Agua_Color_BaseColor.png'));
aguaTexture.wrapS = THREE.RepeatWrapping;
aguaTexture.wrapT = THREE.RepeatWrapping;

const aguaRoughness = new THREE.TextureLoader().load(
    require('../../../assets/models/mall/Textures/Agua/CC_Agua_Color_Roughness.png'));
aguaRoughness.wrapS = THREE.RepeatWrapping;
aguaRoughness.wrapT = THREE.RepeatWrapping;

const aguaMetalic = new THREE.TextureLoader().load(
    require('../../../assets/models/mall/Textures/Agua/CC_Agua_Color_Metallic.png'));
aguaMetalic.wrapS = THREE.RepeatWrapping;
aguaMetalic.wrapT = THREE.RepeatWrapping;

const aguaNormalTexture = new THREE.TextureLoader().load(
    require('../../../assets/models/mall/Textures/Agua/CC_Agua_Color_Normal.png'));
aguaNormalTexture.wrapS = THREE.RepeatWrapping;
aguaNormalTexture.wrapT = THREE.RepeatWrapping;

const aguaMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness: 0.1,
    map: aguaTexture,
    normalMap: aguaNormalTexture,
    roughnessMap: aguaRoughness,
    metalnessMap: aguaMetalic,
    side: THREE.DoubleSide
});

