const tierraMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness: 0.1,
    map: tierraTexture,
    normalMap: tierraNormalTexture,
    aoMap: tierraEmissive,
    lightMap: tierraRoughness,
});

AFRAME.registerComponent('mall-materials', {
    init: function () {
        let el = this.el;

        el.addEventListener("model-loaded", e => {
            let mesh = el.getObject3D('mesh');


            if (!mesh) { return; }
            mesh.traverse(function (node) {

                if (node.name === 'Piso1_0') {
                    console.log(node.position)
                }

                if (node.isMesh) {

                    if (node.material.name.includes('Local')) {
                        node.material.color.set('#ea660e');
                    }
                    switch (node.material.name) {
                        case "Vidrio_Color":
                            node.material = vidrioMaterial;
                            break;
                        case "Agua_Color":
                            node.material = aguaMaterial;
                            break;
                        case "Algas_Color":
                            node.material = algaMaterial;
                            break;
                        case "Arena_Color":
                            node.material = arenaMaterial;
                            break;
                        case "Bench_Color":
                            node.material = benchMaterial;
                            break;
                        case "Coral_Color":
                            node.material = coralMaterial;
                            break;
                        case "Estrellas_Color":
                            node.material = estrellasMaterial;
                            break;
                        case "Flower_Petal_2_Color":
                            node.material = florMaterial2;
                            break;
                        case "Flower_Petal_4_Color":
                            node.material = florMaterial4;
                            break;
                        case "Neon_Color":
                            node.material = neonMaterial;
                            break;
                        case "Palos_Color":
                            node.material = palosMaterial;
                            break;
                        case "Pared_Color":
                            node.material = paredMaterial;
                            break;
                        case "Puerta_Color":
                            node.material = puertaMaterial;
                            break;
                        case "Tierra_Color":
                            node.material = tierraMaterial;
                            break;

                    }
                }
            });


        });
    },

});
