import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { changeColor } from '../../../aframe-components/AdminComponents/changeMat';
import { changePubliImage } from '../../../aframe-components/AdminComponents/changeMat';
import { updatePubli } from '../../../../server/UpdatePubli';


export default function MainEstanteria(props) {
    const [imageData, setImageData] = React.useState();
    const [array, setArray] = React.useState(JSON.parse(props.arrobject.posicion_publicidad));

    React.useEffect(() => {

        
            const image_input = document.getElementById("image-input");
            
            image_input.addEventListener("change", function() {
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                const uploaded_image = reader.result;
                
                
                setImageData(uploaded_image)
                changePubliImage(props.arrobject.clave_publicidad,uploaded_image)
                console.log(props.arrobject.clave_publicidad)
               
            });
            reader.readAsDataURL(this.files[0]);
            });
        
        
    
      },[]);

    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "13%",

        padding: '2rem 0.5rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        width: "33%",
        padding: '0rem 0.5rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        width: "50%",
        padding: '0.5rem 1rem'
    }

    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    const colorContainer = {
        width: "100%",
        display: "flex",
        flexWrap: "wrap"

    }

    const myStyle3 = {
        display: "flex",
        height: "100%",
        width: "100%",
        flexDirection: "column",
        padding: '1rem 1rem'
    }

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#ea660e",
        '&:hover': {
          backgroundColor: "#d65d0d",
        },
      }));


   

    const handleSave = () => {
        //console.log(props.arrobject)
        console.log(props)
        //console.log(document.getElementById('nombre-input').value)
        let jsonPubli = JSON.parse(props.arrobject.posicion_publicidad)
        let nombre = document.getElementById('nombre-input').value
        let descripcion = document.getElementById('descrip-input').value

        let dataPubli = {
            categoria: jsonPubli.categoria,
            nombre: nombre===""?jsonPubli.nombre:nombre,
            descripcion: descripcion===""?jsonPubli.descripcion:descripcion
        }

        //console.log(dataPubli)
        updatePubli(props.arrobject,JSON.stringify(dataPubli))
        
    };


    return (


        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            <Box sx={backDivStyle}>
                <button style={styleButton} onClick={()=>props.handleClickBack(props.arrobject.clave_gondola_tipo_1)}>
                    <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                    <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                </button>


            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle}>
                {/* <Box sx={{ width: "45%", height: "70%" }}>
                    <img src={require("../../../../assets/images/prev_estantes/" +props.imgshelf)} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                </Box> */}
                <Box sx={myStyle3}>
                            <Box sx={{ width: "100%" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Imagen</Typography>

                            </Box>
                            <Box sx={{ width: "100%", height: "50%", display: "flex" }}>
                                <Box sx={{ width: "50%", height: "100%", backgroundColor: "white" }}>
                                    <img src= {imageData} style={{width:"100%", height:"100%", objectFit:"scale-down"}}></img>
                                </Box>
                                <Box>
                                    <button className="o-button-style custom-input-file" style={{ display: "flex", height:"auto", marginLeft: "0.2rem" }}>
                                        <FolderRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                        <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Elegir archivo</Typography>
                                        <input type="file" id="image-input" className="input-file" accept="image/jpeg, image/png, image/jpg">
                                        </input>
                                    </button>

                                    <Box sx={{ display: "flex", marginTop: "0.5rem", marginLeft: "0.2rem" }}>
                                        <button className="o-button-style" style={{ display: "flex", height:"auto", marginLeft: "0.2rem" }} 
                                            onClick={()=>{
                                                /* setImageData("")
                                                changeLogo("","Local"+props.number) */
                                            }}>
                                            <DeleteRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                            <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Eliminar</Typography>
                                        </button>
                                        
                                    </Box>
                                </Box>
                            </Box>

                        </Box>
                
                

            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle2}>
                <Box sx={{ width: "40%", height: "70%" }}>
                    <Typography sx={{ color: 'white', fontSize: "0.9rem" }} variant='h6'>Nombre:</Typography>
                    <input id="nombre-input" style={{ width: "80%" }} className="o-input" type="text" defaultValue={array.nombre}></input>

                </Box>
                <Box sx={{ width: "40%", height: "70%", marginRight:"1rem" }}>

                    <Typography sx={{ color: 'white', fontSize: "0.9rem" }} variant='h6'>Descripcin:</Typography>
                    <input id="descrip-input" className="o-input" type="text" style={{ height: "3rem", width: "100%" }} defaultValue={array.descripcion}></input>
                </Box>
                <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#ea660e", height:"3rem" }}>
                    Guardar
                </ColorButton>
            </Box>

        </Box>


    )
}