import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';
import MainEstanteria from './MainEstanteria';

import { config_shelf } from '../../../scripts/EstanteriasAdmin';
import { leave_shelf } from '../../../scripts/EstanteriasAdmin';

import addLogo from '../../../../assets/images/logos_loacles/addLogo.jpg'

export default function ConfigEstanterias() {

    const [infoOpen, setInfoOpen] = React.useState(false);
    const [infoShelf, setInfoShelf] = React.useState();
    const [imgShelf, setImgShelf] = React.useState();

    function openEstanteria(id, obj) {

        let ubi 

        /* if(obj.ubicacion_gondola_tipo_1){
            ubi = JSON.parse(obj.ubicacion_gondola_tipo_1.replace(/['"]+/g, '"'))
            setImgShelf("Prev_Estante_1.jpg")
        }else if(obj.ubicacion_gondola_tipo_2){
            ubi = JSON.parse(obj.ubicacion_gondola_tipo_2.replace(/['"]+/g, '"'))
            setImgShelf("Prev_Estante_2.jpg")
        }else if(obj.ubicacion_gondola_tipo_3){
            ubi = JSON.parse(obj.ubicacion_gondola_tipo_3.replace(/['"]+/g, '"'))
            setImgShelf("Prev_Estante_3.jpg")
        } */


        //config_shelf(id, "Logos")

        //console.log(id)
        //console.log(obj)
        setInfoOpen(true)
        setInfoShelf(obj)

        //document.getElementById("estantes-camera").setAttribute('camera', 'active', true)
        //document.getElementById("estantes-camera").setAttribute('orbit-controls', 'target',{x:ubi.position.x -13,y:1.6,z:ubi.position.z-11})
        
        //console.log(x)
    }

    function backEstanteria(id) {
        setInfoOpen(false)
        //leave_shelf(id)
        //document.getElementById("player-camera").setAttribute("camera", { active: true })
    }


    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    function loadCards() {
        const arr = JSON.parse(localStorage.getItem('arrpubli'));

        console.log(arr)
        let rows = [];
        let key
        let id
        let image
        let area

        arr.sort((a, b) => a.clave_publicidad - b.clave_publicidad);

        arr.forEach(el => {
            id = el.id_publicidad
            key = el.clave_publicidad
            image = el.imagen_publicidad
            area = JSON.parse(el.posicion_publicidad).categoria.toUpperCase()

            if(area === localStorage.getItem('areaSel')){
                rows.push(
                    <CustomCard arrobject={el} key={id} id={id} size="sm" number={key} type={1} image={image} handleClick={openEstanteria}>
    
                    </CustomCard>
                )
            }
            
        })
        
        

        return <Box sx={{ display: "flex", alignItems: "flex-end", padding: "1rem 1rem 0.3rem 1rem" }}>{rows}</Box>;

    }


    return (
        <Box sx={{ width: "97%", height: "100%", display: "flex" }}>
            <Box sx={{ width: '15%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Configuracin contenido</Typography>
            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />

            {/* Dividir en componentes */}
            <Box sx={{ width: '85%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                <Box sx={{ width: '90%', height: '80%', backgroundColor: '#676767', display: 'flex', overflowX:'auto' }}>

                    {/* Pestaas superiores */}
                    <Box sx={{ width: "auto", height: "1.8rem", position: "absolute", top: "0", display: "flex" }}>
                        <Box id="tab-config" className="o-tab-active" sx={pestanaStyle}><Button sx={{ padding: 0 }} variant="text" id="btn-config">Configuracin</Button></Box>
                    </Box>

                    {!infoOpen &&
                        loadCards()
                        
                    }



                    {infoOpen &&
                        <MainEstanteria handleClickBack={backEstanteria} arrobject={infoShelf} imgshelf={imgShelf}></MainEstanteria>
                    }


                </Box>
            </Box>
        </Box>
    )
}